import SparkMD5 from "spark-md5";
export const uploadFile = {
  data() {
    return {
      fileList: []
    };
  },
  methods: {
    // 
    beforeRead(file) {
      let imageRegex = /^image\/(jpeg|jpg|png)/;
      if (!imageRegex.test(file.type)) {
        alert("jpeg,jpg,png");
        return false;
      }
      const formdata = new FormData();
      formdata.append("uploadType", "image");
      // 
      this.uploadFile(formdata, file);
    },
    // md5
    getFileMD5(file, callback) {
      //
      var fileReader = new FileReader(),
        //2M
        chunkSize = 10485760,
        chunks = Math.ceil(file.size / chunkSize),
        currentChunk = 0,
        //md5SparkMD5
        spark = new SparkMD5();

      //
      fileReader.onload = function(e) {
        //sparkMD5
        spark.appendBinary(e.target.result);
        currentChunk++;

        //MD5
        if (currentChunk < chunks) {
          // loadNext();
          callback(spark.end());
        } else {
          callback(spark.end());
        }
      };

      //
      function loadNext() {
        var start = currentChunk * chunkSize,
          end = start + chunkSize >= file.size ? file.size : start + chunkSize;

        fileReader.readAsBinaryString(file.slice(start, end));
      }
      loadNext();
    },
    // 
    uploadFile(formdata, file) {
      formdata.append("file", file);
      formdata.append("fileName", file.name);
      formdata.append("__sid", this.$userInfo.sessionId);
      // md5
      this.getFileMD5(file, md5 => {
        // md5
        formdata.append("fileMd5", md5);
        this.$api.common
          .uploadFile(formdata)
          .then(res => {
            if (res.result === "true") {
              this.$Toast.success({
                message: ""
              });
              this.uploadFileSuccess(res);
            } else {
              this.$Toast.fail({
                message: ""
              });
            }
          })
          .catch(() => {
            this.$Toast.fail({
              message: ""
            });
          });
      });
    },
    // 
    uploadFileSuccess(res) {
      let urls = this.$imageUrl + res.fileUpload.fileUrl;
      let obj = {
        index: this.fileList.length,
        id: res.fileUpload.id,
        url: urls,
        fileUrl: res.fileUpload.fileUrl
      };
      this.fileList.push(obj);
    },
    // 
    beforeDelete(e) {
      let _fileList = this.fileList.filter(item => {
        return !Object.is(e, item);
      });
      this.fileList = _fileList;
    }
  }
};
